import './App.css';
import { useCallback, useEffect, useState } from 'react';

const cronParser = require('cron-parser');

function App() {

  const [value, setValue] = useState('* * * * * *');
  const [message, setMessage] = useState();

  const handleChange = useCallback((value) => {
    setValue(value);
  }, [value]);

  useEffect(() => {
    try {
      const datetime = Date.now()
      let options =  {
        // get next value with respect to current date
        currentDate: new Date(datetime)
      }
      const interval = cronParser.parseExpression(value, options);
      if (!interval.hasNext()) {
        throw new Error(`Error while parsing cron expression ${value} for next date`)
      }
      const ranking_time = new Date(interval.next().toString());
      setMessage(value +' --> ' + ranking_time.toDateString() + ' ' + ranking_time.toTimeString());
    } catch(e) {
      setMessage('Error bad format '+ value + ' Error :' + e.message);
    }
  }, [value])


  return (
    <div className="App">

      Cron Parser format: 
      <br />
      <br />
      <input type="text" value={value} onChange={(e) => handleChange(e.target.value)} />

      <div>
        {message}
      </div>

    </div>
  );
}

export default App;
